.white-header {
    display: flex;
    align-items: center;
    background-color: white;
    border-radius: 70px;
    justify-content: center;
    width: 140px;
    height: 140px;
    transition: transform 2s;
}

@keyframes rotate {
    from {transform: rotate(0deg);}
    to {transform: rotate(360deg);}
}